import { Component, OnInit } from '@angular/core';

import { GUI } from 'dat.gui';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  title = 'app';

  gui: GUI = new GUI();
  guiInputs = {
    particleCount: 256,
    rField: {
      coefficient: 1,
      size: 64
    }
  };

  ngOnInit() {
    const guiContainer = document.getElementById('dat-gui-container');
    this.setupDatGui(this.gui, this.guiInputs, guiContainer);
  }

  private setupDatGui(gui: GUI, data, container) {
    container.appendChild(gui.domElement);
    gui.add(data, 'particleCount', 32, 2048);

    const rFieldGui = gui.addFolder('Repulsion Field');
    rFieldGui.add(data.rField, 'coefficient').min(0.001).max(10).step(0.0001);
    rFieldGui.add(data.rField, 'size').min(1).max(300).step(1);
  }
}

